.allHeroesList {
    margin-top: 2rem;
    
    &__cardsWrap {
        max-width: 90%;
        margin-left: auto;
        margin-right: auto;
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        justify-content: space-between;
    }

    &__alert {
        font-family: 'Geologica', sans-serif;
        font-weight: 300;
        font-size: 1.25rem;
        margin-top: 5rem;
        margin-left: 1rem;
        margin-bottom: 0.5rem;
    }
}